import React, { useEffect, useState } from "react";
import { Provider } from "react-redux";
import { useNavigate, Routes, Route } from "react-router-dom";
import { OktaAuth, toRelativeUrl } from "@okta/okta-auth-js";
import { Security, useOktaAuth } from "@okta/okta-react";
import config from "./../config/config";
import { Store } from "./redux/Store";
import Router from "./routes";
import { GlobalCss, MatxSuspense, MatxTheme } from "app/components";
import { ErrorBoundary } from "react-error-boundary";
import Fallback from "./components/Fallback/Fallback";
import { OracleMappingsProvider } from "./contexts/OracleSettingsContext";
import { SettingsProvider } from "./contexts/SettingsContext";
import DragAndDropComponent from "DragAndDrop/DragAndDrop";
import CustomLoginCallback from "./CustomLoginCallback";

const AppContent = ({ oktaAuth }) => {
  const { authState } = useOktaAuth();

  if (!authState || !authState.accessToken || !authState.isAuthenticated) {
    console.log(" Waiting for auth state...");
    return <div>Loading authentication...</div>;
  }

  return (
    <OracleMappingsProvider>
      <Router />
    </OracleMappingsProvider>
  );
};

const App = () => {
  const navigate = useNavigate();
  const [hasDropped, setHasDropped] = useState(false);
  const [oktaAuth, setOktaAuth] = useState(null);

  const restoreOriginalUri = (_oktaAuth, originalUri) => {
    navigate(toRelativeUrl(originalUri || "/", window.location.origin));
  };

  const onAuthRequired = () => {
    navigate(window.location.origin);
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      performance.mark("FirstContentfulPaint");
    }, 1000);
    return () => clearTimeout(timer);
  }, []);

  if (!hasDropped) {
    return (
      <DragAndDropComponent
        onDropSuccess={() => {
          setOktaAuth(new OktaAuth(config.oidc));
          setHasDropped(true);
        }}
      />
    );
  }

  if (!oktaAuth) {
    return <div>Initializing authentication...</div>;
  }

  return (
    <ErrorBoundary FallbackComponent={Fallback} onReset={() => {}}>
      <Provider store={Store}>
        <SettingsProvider>
          <MatxTheme>
            <GlobalCss />
            <MatxSuspense>
              <Security
                oktaAuth={oktaAuth}
                restoreOriginalUri={restoreOriginalUri}
                onAuthRequired={onAuthRequired}
              >
                {/*  This is the key fix: Always render LoginCallback */}
                <Routes>
                  <Route
                    path="/login/callback"
                    element={<CustomLoginCallback />}
                  />
                  <Route
                    path="*"
                    element={<AppContent oktaAuth={oktaAuth} />}
                  />
                </Routes>
              </Security>
            </MatxSuspense>
          </MatxTheme>
        </SettingsProvider>
      </Provider>
    </ErrorBoundary>
  );
};

export default App;
